section.why {
  padding-top: 48px;
  padding-bottom: 64px;

  .gt(@break-point-desktop, {
    padding-top: 80px;
    padding-bottom: 75px;
  });

  /* mobile */

  h2 {
    margin-bottom: 24px;

    .gt(@break-point-desktop, {
      border: @border;
      border-bottom: 0;
      padding: 25px;
      margin-bottom: 0;
    });
  }

  .buttons {
    display: none;
  }

  ol.slides {
    counter-reset: why-counter;

    li {
      border: @border;
      border-bottom: 0;
      padding: @m-padding;

      &:last-child {
        border-bottom: @border;
      }

      &:before {
        font-family: @font-title;
        font-weight: 300;
        font-size: 60px;
        line-height: 91%;

        display: block;
        counter-increment: why-counter;
        content: counter(why-counter);
        float: left;
        min-width: 30px;
        margin-right: 12px;
      }

      h3 {
        font-size: 22px;
        line-height: 30px;
        text-align: left;
        margin-bottom: 12px;
      }

      p {
        font-size: 14px;
        line-height: 140%;
        margin-bottom: 8px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .image {
        display: none;
      }
    }

  }

  /* desktop */
  .slider {
    @media (min-width: @break-point-desktop) {
      counter-reset: why-buttons;
      display: flex;
      border-bottom: @border;

      .buttons {
        display: flex;
        flex-direction: column;

        & > li {
          flex-grow: 2;
        }

        button {
          appearance: none;
          width: 64px;
          height: 100%;
          border: @border;
          border-bottom: 0;
          background-color: unset;
          cursor: pointer;

          font-family: @font-title;
          font-size: 27px;
          color: inherit;

          &:focus,
          &:hover {
            background-color: @main-color;
            color: #000000;
            outline: @border-width solid @main-color;
            outline-offset: -2px;
          }

          &.current {
            background-color: @bg-accent;
            color: @main-color;

            &:hover,
            &:focus {
              outline-color: transparent;
            }
          }

          &::before {
            counter-increment: why-buttons;
            content: counter(why-buttons);
          }
        }
      }

      .slides {

        flex-grow: 2;

        .slide {
          display: none;
        }

        .current {
          display: flex;
          flex-direction: row;
          border-left: 0;
          height: 100%;
          padding: 0;

          &::before {
            content: '';
            display: none;
          }

          .text {
            padding: 40px;

            h3 {
              font-size: 32px;
              line-height: 42px;
              margin-bottom: 20px;
            }

            p {
              font-size: 18px;
              line-height: 140%;
              margin-bottom: 16px;
            }
          }

          .image {
            display: block;
            border: @border;
            border-width: 4px;
            position: relative;
            cursor: pointer;

            &::before {
              content: '';
              display: block;
              position: absolute;
              left: -4px;
              bottom: -4px;
              background-color: @border-color;
              background-image: url(./expand.svg);
              background-repeat: no-repeat;
              background-position: center;
              background-size: 50%;
              width: 40px;
              height: 40px;
            }

            &:hover {
              &::before {
                width: 56px;
                height: 56px;
              }
            }

            img {
              display: block;
            }
          }
        }
      }
    }
  }
}

section.why {
  .gt(@break-point-pad, {
    padding-bottom: 24px;
    });
}
